@import "../libs/include-media/dist/include-media";
@import "utils/special-version-variables";
@import "utils/functions";
@import "utils/mixins";
@import "utils/placeholders";

/*=========== SPECIAL VERSION =========== */
.vspec--hide-page {
	display: none !important; }

body.#{$spec-mod} {
	font-size: $spec-font-size;
	line-height: $spec-line-height;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
	//font-family: Tahoma, Geneva, sans-serif
	//font-family: "Times New Roman", Serif
	color: $spec-text-color;
	background: $spec-bg-color url(../img/empty.png) no-repeat fixed; }

/* special version settings */
/* ============================================================ */

body.#{$spec-mod} {
	// Специальная версия: Уменьшенный базовый шрифт
	// ================================================================
	.#{$spec-mod--font-small} & {
		font-size: $spec-font-size--small; }

	// Специальная версия: Увеличенный базовый шрифт
	// ================================================================
	.#{$spec-mod--font-large} & {
		font-size: $spec-font-size--large; }

	// Специальная версия: Средний межбуквенный интервал
	// ================================================================
	.#{$spec-mod--spacing-medium} & {
		letter-spacing: $spec-letter-spacing--medium; }

	// Специальная версия: Бальшой межбуквенный интервал
	// ================================================================
	.#{$spec-mod--spacing-large} & {
		letter-spacing: $spec-letter-spacing--large; }

	// Специальная версия: Цветовая схема - Белым по черному
	// ================================================================
	.#{$spec-mod--schemecolor-wb} & {
		color: $spec-text-color--scheme-wb;
		background-color: $spec-bg-color--scheme-wb; }

	// Специальная версия: Цветовая схема - Синим по голубому
	// ================================================================
	.#{$spec-mod--schemecolor-blue} & {
		color: $spec-text-color--scheme-blue;
		background-color: $spec-bg-color--scheme-blue; } }

/* special version reset */
/* ============================================================ */

// reset all styles
// ------------------------------------------------------------
@import "spec/reset-full";

// reset font
// ------------------------------------------------------------
@import "spec/fonts-spec";

// reset transition
// ------------------------------------------------------------
@import "spec/transition";

// hide elements
// ------------------------------------------------------------
@import "spec/hide";

// reset svg icon color
// ------------------------------------------------------------
@import "spec/svg";

// special version background color
// ------------------------------------------------------------
@import "spec/bg";

// special version background and text color invert
// ------------------------------------------------------------
@import "spec/bg-invert";

// special reset padding
// ---------------------------------------------------------------------------
@import "spec/padding";

// special vertical space
// ---------------------------------------------------------------------------
@import "spec/spaces";

// reset images
// ------------------------------------------------------------
@import "spec/img";

// reset slider
// ------------------------------------------------------------
@import "spec/slider";

// layout spaces
// ------------------------------------------------------------
@import "spec/layout";

/* special version typography */
/* ============================================================ */

@import "spec/typography";

/* special version components */
/* ============================================================ */

/* to top */
.#{$spec-mod} {
	.btn-to-top {
		opacity: 0.5;

		i {
			&::before, &::after {
				background-color: currentColor; } }

		.no-touchevents &:hover {
			opacity: 0.9; } } }

/* to top end */

.#{$spec-mod} {
	.text-layout, .breadcrumbs-list, .user-content .breadcrumbs-list, .expander__header, .calc__box {
		padding-left: 0;
		padding-right: 0; }

	.header {
		color: currentColor; }

	.header-holder {
		padding-top: 0; }

	.main {
		padding-top: $spec-bottom-space-section;
		padding-bottom: $spec-bottom-space-section; }

	.content {
		//border: $spec-border-width solid $spec-text-color
		margin-left: 0;
		background: none;
		border: none; }

	.footer {
		border-top: $spec-border-width solid currentColor; }

	.footer,
	.footer-gov,
	.footer-bottom {
		background: none; }

	.footer-common {
		padding-top: 1em; }

	.footer-bottom-holder {
		padding-top: 1em;
		padding-bottom: 1em;
		@include clear; }

	.footer-col {
		float: left;
		font-size: $spec-font-size;
		line-height: $spec-line-height;
		letter-spacing: $spec-letter-spacing;

		&:last-child {
			float: right; } }

	.developer_logo {
		display: inline;
		text-indent: 0;
		padding: 0;
		width: auto;
		height: auto;
		top: auto;

		&::before, &::after {
			display: none; } }

	// logo
	.logo-title > strong {
		background-image: url(../img/logo-text--bw.svg);
		background-size: contain;

		.#{$spec-mod--schemecolor-wb} & {
			background: url(../img/logo-text--wb.svg) no-repeat; }

		.#{$spec-mod--schemecolor-blue} & {
			background: url(../img/logo-text--blue.svg) no-repeat; } }

	// main-nav
	.main-nav-frame {
		box-shadow: none;
		transition: none; }

	.nav-fixed & .main-nav-frame {
		&:before {
			display: none; } }

	.main-nav-holder {
		$shadow: 0 0 0 $spec-border-width;
		box-shadow: $shadow $spec-text-color;
		transition: none;

		.#{$spec-mod--schemecolor-wb} & {
			box-shadow: $shadow $spec-text-color--scheme-wb; }

		.#{$spec-mod--schemecolor-blue} & {
			box-shadow: $shadow $spec-text-color--scheme-blue; } }

	// navigation list
	.nav__list {
		& > li {
			& > a {
				color: currentColor;
				@include spec-reset-bg;
				border: 0 solid currentColor;
				border-left-width: $spec-border-width;

				&:before {
					display: none; } }

			&:first-child > a {
				border-left-width: 0; }

			// hover
			&.hover > a,
			& > a:hover,
			& > a:focus {
				color: currentColor;
				text-decoration: none;
				border-color: currentColor; }

			&.has-drop.hover,
			&.current.hover_next,
			&.current.hover_prev {
				& > a {
					border-color: currentColor; } }

			// current
			&.current > a {
				text-decoration: none;
				@include spec-reset-bg-invert; } }

		[class*="nav-depict-icons-angle"] {
			&::before, &::after {
				background-color: currentColor; } } }

	[class*="nav__drop"] {
		border: $spec-border-width solid currentColor;
		box-shadow: none;

		a {
			padding: 10px 40px;
			@include font($spec-font-size, $spec-line-height);
			background: none;
			border-top: $spec-border-width solid currentColor; }

		li.hover > a {
			background: none; } }

	[class*="nav__list"] > li:first-child > [class*="nav__drop"] {
		border: $spec-border-width solid currentColor; }

	// button open menu / close popups

	.btn-menu, .btn-menu-close {
		display: block;
		display: flex;
		justify-content: center;
		align-items: center;
		color: $spec-text-color;
		transition: none;
		box-sizing: border-box;
		border: 0 solid currentColor;
		background-color: $spec-bg-color;
		text-decoration: underline;

		.#{$spec-mod--schemecolor-wb} & {
			background-color: $spec-bg-color--scheme-wb;
			color: $spec-text-color--scheme-wb; }

		.#{$spec-mod--schemecolor-blue} & {
			background-color: $spec-bg-color--scheme-blue;
			color: $spec-text-color--scheme-blue; }

		&::before {
			display: none; }

		& > span {
			display: inline;
			vertical-align: middle;
			font-size: $spec-font-size;
			font-weight: bold;
			letter-spacing: $spec-letter-spacing;
			text-transform: uppercase; }

		& > i {
			display: none;

			&, &::before, &::after {
				transition: none;
				background: currentColor; } } }

	.btn-menu {
		border-right-width: $spec-border-width; }

	.btn-menu-mobile {
		display: none; }

	.btn-menu-close {
		width: 120px;
		border-width: $spec-border-width;

		& > i {
			background: none; } }

	.user-options {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		position: relative;
		top: auto;
		right: auto;
		width: auto;
		padding: 0;
		margin-bottom: 0.5em;
		@include spec-font-small;

		li {
			padding: 0;
			margin: 0 1em 0.25em 0; }

		a {
			color: currentColor;

			&::before {
				display: none; }

			span {
				display: inline;
				border: none;
				line-height: inherit;
				transition: none;

				&.text-for-mobile {
					display: none; } } }

		svg {
			//display: none

			path {
				fill: currentColor; } } }

	// breadcrumbs
	.breadcrumbs-list {
		padding: 0;
		height: auto;
		min-height: 0;
		overflow: visible;
		@include spec-font-small;

		li, a, span {
			display: inline;
			vertical-align: baseline; }

		li {
			display: inline;
			padding: 0 0.4em 0 1em;

			&::before {
				color: inherit; }

			&:first-child {
				padding-left: 0; }

			span {
				line-height: inherit;
				border: none !important;
				white-space: normal; } }

		a {
			&:hover, &:focus {
				color: inherit; } } }

	// social networks
	.social-networks {
		li {
			padding-left: 0; }

		a {
			&::before {
				transition: none;
				border-color: currentColor; } }

		path {
			fill: currentColor; }

		// hover
		a:hover {
			path {
				fill: currentColor; } } }

	// content foot
	.content-foot {
		border: none;
		margin: 0;
		padding-top: $spec-bottom-space-section; }

	.user-content > .text-layout + .content-foot {
		margin-top: 0 !important; }

	.content-foot .share-box + div {
		border-top: $spec-border-width solid currentColor; }

	// share box
	.share-box__label {
		float: none; }

	.share-box {
		padding-bottom: $spec-bottom-space;

		.social-networks {
			float: none;
			margin: 0;
			padding: 0;
			text-align: left; } }

	// nav page
	.nav-page {
		font-size: inherit;
		letter-spacing: inherit;
		height: auto;
		padding: $spec-bottom-space 0 0;
		border-top: $spec-border-width solid currentColor; }

	.nav-page__item {
		display: block;
		border: none;
		float: none;
		text-align: left;
		color: inherit;

		a {
			display: inline;
			color: inherit;
			background: none;

			& > span {
				display: inline;
				vertical-align: initial;
				padding: 0;
				width: auto;
				font-size: inherit;
				line-height: inherit;
				font-family: inherit;
				text-transform: none;
				font-weight: bold;
				letter-spacing: inherit; }

			i {
				display: none;

				&::before, &::after {
					background-color: currentColor !important; } }

			&:hover {
				color: inherit;
				background-color: inherit; } }

		// disabled
		&.disabled {
			background: none;

			& a, & a:hover {
				text-decoration: none;
				cursor: default;
				color: inherit;
				opacity: 0.5;
				background-color: inherit; } } }

	.nav-articles .nav-page__item {
		width: auto; }

	// promo-slider
	.promo-slider {
		margin-left: 0; }

	.promo-slider__item {
		border: none;

		&::before {
			display: none; } }

	.promo-slider__bg {
		position: relative;
		left: auto;
		top: auto;
		height: auto;
		width: 320px;

		&::before {
			content: '';
			display: block;
			@include ratio(16, 9); } }

	.promo-slider__content {
		position: relative;
		left: auto;
		top: auto;
		height: auto;
		width: auto;
		text-align: left;
		background: none;

		&::before {
			display: none; } }

	.promo-slider__frame {
		display: block;
		width: auto;
		max-width: none; }

	// regard
	.regard-section {
		background: none;
		box-shadow: none; }

	.regard__item {
		padding: 0;
		width: auto;
		float: none;
		height: auto;
		background: none !important;
		opacity: 1;

		&::before {
			display: none; } }

	.regard__inner {
		position: relative;
		left: auto;
		top: auto;
		right: auto;
		bottom: auto;
		padding: 0;
		text-decoration: underline;

		&:hover {
			text-decoration: none; }

		&::before {
			display: none; }

		& > span {
			display: inline;
			vertical-align: baseline;
			width: auto;
			@include spec-font-item;
			font-weight: bold;
			text-transform: uppercase; } }

	[class*="depict-regard"] {
		display: inline-block;
		vertical-align: baseline; }

	// links
	.r-tabs {
		.tab-controls {
			display: none; }

		.r-tabs-accordion-title {
			display: block; }

		.r-tabs-anchor {
			text-decoration: none;
			cursor: default; }

		.tabs-panel {
			display: block !important; } }

	.links__list {
		.links__item {
			display: block;
			padding: 0;
			width: auto;
			box-shadow: none;

			&.links__empty {
				display: none; } } }

	.links__inner {
		display: inline;
		box-shadow: none;
		background: none;
		text-decoration: none;

		&::before {
			display: none; } }

	.links__holder {
		display: inline;
		position: relative;
		left: auto;
		top: auto;
		width: auto;
		height: auto;

		&::after {
			display: none; } }

	.links__frame {
		width: auto;
		display: inline;
		text-align: left;
		vertical-align: baseline; }

	.links__icon {
		display: inline-block;
		vertical-align: baseline;
		margin-right: 1em;

		svg {
			polygon, rect, path {
				transition: none;
				fill: currentColor; } } }

	.no-touchevents & a:hover .links__icon {
		svg {
			polygon, rect, path {
				fill: currentColor; } } }

	.links__content {
		max-width: none;
		display: inline-block;
		vertical-align: baseline; }

	.links__title {
		display: inline;
		max-height: none;
		overflow: visible;
		text-decoration: underline;

		br {
			display: none; } }

	// hover
	.no-touchevents & .links__inner:hover {
		box-shadow: none;
		background: none;

		.links__title {
			color: inherit;
			text-decoration: none; } }

	// info center
	.info-center-header {
		font-weight: bold; }

	.info__list {
		text-align: left;
		margin: 0; }

	.info__item {
		display: block; }

	.info__inner {
		width: auto;
		height: auto;
		overflow: visible;
		padding: 0;
		background: none;
		box-shadow: none;
		border: none; }

	.info__label {
		display: inline-block;
		vertical-align: top;
		position: relative;
		left: auto;
		top: auto;
		overflow: visible;
		@include spec-font-small;
		font-weight: normal;
		text-transform: uppercase; }

	.info__img {
		transform: none;
		background: none;
		overflow: visible;

		&::before {
			content: none; } }

	.info__figure {
		position: relative;
		left: auto;
		top: auto;
		width: auto;
		height: auto;
		transform: none;
		text-align: left;

		img {
			margin: 0; } }

	.info__content {
		margin-bottom: $spec-bottom-space-element; }

	.info__title, .info__text {
		transform: none;
		opacity: 1;
		visibility: visible;

		.text-wrap {
			max-height: none;
			overflow: visible; } }

	.btn__info {
		margin: 0; }

	.product__price {
		.byn, .byr {
			display: inline;
			color: inherit;
			font-size: inherit;
			font-weight: bold;

			&::before {
				display: none; } }

		.price-unit {
			@include spec-font-small; } }

	// hover
	.no-touchevents & a.info__inner:hover {
		box-shadow: none; }

	.no-touchevents & .show-animation a:hover {
		.info__img,
		.info__content {
			opacity: 1;
			visibility: visible;
			transform: none; } }

	//branches
	.branches {
		padding-left: 0; }

	.branches-map-svg {
		path, polygon {
			stroke: currentColor;
			transition: none;
			fill: $spec-bg-color;

			.#{$spec-mod--schemecolor-wb} & {
				fill: $spec-bg-color--scheme-wb; }

			.#{$spec-mod--schemecolor-blue} & {
				fill: $spec-bg-color--scheme-blue; } }

		text {
			fill: currentColor;
			transition: none;
			letter-spacing: normal; } }

	// hover
	.no-touchevents &, & {
		.branches-map-svg {
			a:hover, a.active {
				path, polygon {
					fill: currentColor;
					stroke: currentColor; }

				.branches-map-logo {
					fill: $spec-bg-color;

					.#{$spec-mod--schemecolor-wb} & {
						fill: $spec-bg-color--scheme-wb; }

					.#{$spec-mod--schemecolor-blue} & {
						fill: $spec-bg-color--scheme-blue; } } } } }

	// branches-popup
	.branches-popup__holder {
		border: $spec-border-width solid currentColor;
		box-shadow: none; }

	.branches-popup__close {
		&::before, &::after {
			background-color: currentColor; }

		.no-touchevents &:hover {
			&::before, &::after {
				background-color: currentColor; } } }

	.branches-popup__cornet {
		display: none; }

	.branches-popup__title, .branches-popup__subtitle {
		color: currentColor; }

	.branches-popup dl svg path {
		fill: currentColor;
		stroke: currentColor; }

	// user form
	.user-form {
		background: none;

		.input-wrap {
			&::before, &:after {
				display: none; } }

		.label-holder {
			float: none;
			width: auto;
			padding: 0;
			margin-bottom: 3px;

			&::before {
				display: none; }

			label, .label {
				max-width: none;
				display: inline;
				vertical-align: initial; } }

		.input-holder {
			margin-left: 0; }

		.form-footer {
			margin-left: 0;
			padding-left: 0;
			display: flex;
			align-items: center; }

		.form-note {
			height: auto;

			&::before {
				display: none; } }

		.form-note__item {
			width: auto;
			display: block; } }

	// tabs
	.custom-tabs__panels {
		border-color: currentColor;
		border-width: 0 $spec-border-width $spec-border-width;
		background: none; }

	.custom-tabs__nav {
		margin-bottom: -$spec-border-width; }

	.custom-tabs__tab {
		font-family: inherit;
		color: inherit;

		a {
			display: flex;
			align-items: center;
			justify-content: center;
			border: $spec-border-width solid currentColor;
			background: none;

			& > span {
				font-size: 24px; }

			&::before {
				display: none; }

			&.active {
				color: currentColor;
				background: none;
				border-bottom-color: transparent;
				text-decoration: none;
				cursor: default; }

			.no-touchevents &:hover {
				background: none; } } }

	.years, .sorting {
		position: relative;
		top: auto;
		right: auto;
		margin-right: 0;
		border: none;
		width: 8em;
		height: auto;

		a {
			padding: 0.1em 0.5em;

			& > span {
				display: inline;
				vertical-align: initial;
				transition: none; } }

		& > a {
			position: relative;
			border: none;
			text-align: left;
			padding-right: 1.5em;

			&::before {
				display: none; }

			&::after {
				content: '';
				display: block;
				position: absolute;
				right: 0;
				top: 50%;
				@include size(0.4em);
				border: 0 solid currentColor;
				border-width: 0 0.1em 0.1em 0;
				transform: rotate(0.125turn);
				margin: -0.35em 0.7em 0 0; }

			& > i {
				display: none; } } }

	.years-drop, .sorting-drop {
		border: none;
		padding: 0;
		margin: 0;

		li {
			padding: 0.1em 0; }

		a {
			text-align: left;

			&:hover {
				color: currentColor; }

			&.active {
				text-decoration: none;
				cursor: default;
				background-color: $spec-bg-color;
				color: $spec-text-color;

				.#{$spec-mod--schemecolor-wb} & {
					background-color: $spec-bg-color--scheme-wb;
					color: $spec-text-color--scheme-wb; }

				.#{$spec-mod--schemecolor-blue} & {
					background-color: $spec-bg-color--scheme-blue;
					color: $spec-text-color--scheme-blue; } } } }

	//news
	.news {
		min-height: 0; }

	.news__item {
		border: none; }

	.news__inner {
		display: initial;
		padding: 0;

		&::after {
			display: none; } }

	.news__img {
		float: none;
		margin-top: 0;
		margin-right: 0; }

	.news__data {
		background: none;
		position: relative;
		left: auto;
		top: auto; }

	// hover
	.no-touchevents & .news__inner:hover {
		background: none;

		.news__title {
			color: inherit; } }

	// promo shop
	.promo-shop {
		border: none; }

	//info container
	.info-container__list {
		margin: 0;
		font-size: inherit;
		letter-spacing: inherit; }

	.info-container__item {
		width: auto;
		display: block;
		padding: 0; }

	// filters
	.filters__list {
		@include margin-horizontal(-0.5em);
		font-size: inherit;
		letter-spacing: inherit;

		@include clear; }

