@import "../../libs/include-media/dist/include-media";
@import "../utils/special-version-variables";
@import "../utils/mixins";

.#{$spec-mod} {
	img,
	[class*="depict-files-"],
	.logo-img,
	.promo-slider__bg,
	.depict-regard-number,
	.news__img,
	.grayscale {
		filter: grayscale(1) contrast(1) invert(0); }

	// Показать/скрыть image, icon, figure
	// ------------------------------------------------------------
	html:not(.#{$spec-mod--imageon}) & {

		img,
		[class*="depict-files-"],
		.promo-slider__bg,
		.depict-regard-safety,
		.links__icon,
		.info__img,
		.news__img,
		.spec-no-img {

			display: none !important; } } }
