@import "../../libs/include-media/dist/include-media";
@import "../utils/special-version-variables";
@import "../utils/mixins";

.#{$spec-mod} {
	// reset font-family
	// ------------------------------------------------------------
	//h1, h2, h3, h4, h5, h6
	//ont-family: inherit

	// reset text color
	// ------------------------------------------------------------
	//h1, h2, h3, h4, h5, h6
	//olor: inherit



	// reset text size, line-height, letter-spacing, font-family etc.
	// ------------------------------------------------------------
	.sv-btn-on,
	h1, h2, h3, h4, h5, h6, p,
	.breadcrumbs-list,
	.datetime time,
	blockquote q,
	blockquote > p,
	blockquote cite,
	.files,
	.files .files-item,
	.file,
	.footer-bottom,
	.footer-bottom-holder,
	.footer-col,
	.promo-slider__item,
	.links__list,
	.links__list .links__item,
	.links__holder,
	.links__title,
	.info-center-header,
	.info__item,
	.info__label,
	.branches-popup__holder,
	.form-note,
	.form-note__item,
	.user-options,
	.user-options li,
	.user-options a,
	.years a,
	.years a > span,
	.years-drop > a,
	.sorting a,
	.sorting a > span,
	.sorting-drop > a,
	.info-container__item,
	.filters__item,
	.products__item,
	.font-reset,
	.font-reset {
		@include spec-reset-font; }



	// Шрифт по умолчанию, текст статьи, текст внутренней страницы
	// ---------------------------------------------------------------------------
	.promo-slider__text-add,
	.promo-slider__text,
	.regard__inner,
	.regard__inner > span,
	.info__text,
	.news__text,
	.product__price,
	.products__text,
	.font-of-text,
	.font-of-text {
		padding: 0;
		margin-bottom: $spec-bottom-space-element;
		@include spec-reset-font;

		&:last-child {
			margin-bottom: 0; } }



	// H1, Заголовок страницы
	// ---------------------------------------------------------------------------
	.font-of-title {
		padding: 0;
		margin-bottom: $spec-bottom-space-element;
		font: {
			family: inherit;
			weight: bold;
			style: normal; }
		@include spec-font-title;
		letter-spacing: inherit;
		text-transform: none;
		color: inherit;
		text-align: left;

		&:last-child {
			margin-bottom: 0; } }



	// H2, Заголовки секций
	// ---------------------------------------------------------------------------
	.info-center-caption,
	.font-of-section {
		padding: 0;
		margin-bottom: $spec-bottom-space-element;
		font: {
			family: inherit;
			weight: bold;
			style: normal; }
		@include spec-font-section;
		letter-spacing: inherit;
		text-transform: none;
		color: inherit;
		text-align: left;

		&:last-child {
			margin-bottom: 0; } }



	// H3, Заголовки пунктов, Заголовки элементов в списке
	// ---------------------------------------------------------------------------
	.promo-slider__title,
	.r-tabs .r-tabs-accordion-title,
	.info__title,
	.news__title,
	.share-box__label,
	.info-container__sub-title,
	.products__title,
	.font-of-item,
	.font-of-item {
		padding: 0;
		margin-bottom: $spec-bottom-space-element;
		font: {
			family: inherit;
			weight: bold;
			style: normal; }
		@include spec-font-item;
		letter-spacing: inherit;
		text-transform: none;
		color: inherit;
		text-align: left;

		&:last-child {
			margin-bottom: 0; } }



	// Мелкий текст: сноски, даты и т.д.
	// ---------------------------------------------------------------------------
	.news__data,
	.font-of-small {
		padding: 0;
		margin-bottom: $spec-bottom-space-element;
		font: {
			family: inherit;
			weight: normal;
			style: italic; }
		@include spec-font-small;
		letter-spacing: inherit;
		text-transform: none;
		color: inherit;
		text-align: left;

		&:last-child {
			margin-bottom: 0; } } }
