@import "../../libs/include-media/dist/include-media";
@import "../utils/special-version-variables";
@import "../utils/mixins";

.#{$spec-mod} {
	.someclass,
	.someclass {
		padding: {
			left: $spec-space;
			right: $spec-space; }

		@include media('<mob-lg') {
			padding: {
				left: $spec-space--sm;
				right: $spec-space--sm; } }

		@include media('<mob-md') {
			padding: {
				left: $spec-space--mob;
				right: $spec-space--mob; } } }


	.text-layout .full-width {
		margin-left: 0;
		margin-right: 0; }

	.user-content > .text-layout > *:last-child {
		margin-bottom: 0 !important; } }
